            <div class="fa fa-paper-plane-o" @click="guessNameHandel(x._id,x.guessOk,x.guessAll)"> {{x.guessOk}}/{{x.guessAll}}</div>
            <div class="fa fa-comment-o" @click="commentHandel(x._id,x.comment)"> {{x.comment.length}}</div>
          </section>
