          <section>
            {{x.loveContent}}
            <small class="main-cell-date">{{x.date}}</small>
          </section>
          <section>
            <div class="fa fa-heart-o" @click="isUp(x._id)"> {{x.praise}}</div>
            <div class="fa fa-paper-plane-o" @click="guessNameHandel(x._id,x.guessOk,x.guessAll)"> {{x.guessOk}}/{{x.guessAll}}</div>
            <div class="fa fa-comment-o" @click="commentHandel(x._id,x.comment)"> {{x.comment.length}}</div>
          </section>
        </div>
      </section>
      <section class="main-all" v-show="isCheckLi==2">
        <div v-for="(x,index) in loveSM" :key="index" class="main-cell">
          <section>
            <div :class="{boy:x.mateA==0,girl:x.mateA==1}">{{x.nameA}}</div>
            <div></div>
            <div :class="{boy:x.mateB==0,girl:x.mateB==1}">{{x.nameB}}</div>
          </section>
          <section>
            {{x.loveContent}}
            <small class="main-cell-date">{{x.date}}</small>
          </section>
          <section>
            <div class="fa fa-heart-o" @click="isUp(x._id)"> {{x.praise}}</div>
            <div class="fa fa-paper-plane-o" @click="guessNameHandel(x._id,x.guessOk,x.guessAll)"> {{x.guessOk}}/{{x.guessAll}}</div>
            <div class="fa fa-comment-o" @click="commentHandel(x._id,x.comment)"> {{x.comment.length}}</div>
          </section>
        </div>
      </section>
      <section v-show="isCheckLi==3">
        <div>
          <section class="com-cell com-cell-boy">
            <label class="com-cell-label com-cell-label-boy">{{msgAll.titleA}}</label>
            <p class="nick-a">{{msgAll.nickA}}:</p>
            <input type="text" class="nick-input-b" :placeholder="msgAll.nickAP" v-model="postMsg.nickA">
            <p class="name-a">{{msgAll.nameA}}:</p>
            <input type="text" class="nick-input-b" :placeholder="msgAll.nameAP" v-model="postMsg.nameA">
            <picker @change="bindPickerThrChange" :value="postMsg.mateA" :range="arrayOk">
              <view class="picker">
                {{msgAll.aisAon}}：{{arrayOk[postMsg.aisAon]}}
              </view>
            </picker>
            <small class="nick-sm">{{msgAll.look}}</small>
            <picker @change="bindPickerOneChange" :value="postMsg.aisAon" :range="array">
              <view class="picker">
                {{msgAll.mateA}}：{{array[postMsg.mateA]}}
              </view>
            </picker>
          </section>
          <section class="com-cell com-cell-girl">
            <label class="com-cell-label com-cell-label-girl">{{msgAll.titleB}}</label>
            <p class="name-b">{{msgAll.nameB}}:</p>
            <input type="text" class="nick-input-g" :placeholder="msgAll.nameBP" v-model="postMsg.nameB">

            <picker @change="bindPickerTwoChange" :value="postMsg.mateB" :range="array">
              <view class="picker">
                {{msgAll.mateB}}：{{array[postMsg.mateB]}}
              </view>
            </picker>
          </section>
          <section class="com-cell com-cell-black">
            <label class="com-cell-label com-cell-label-black">{{msgAll.titleC}}</label>
            <div class="com-div">
              <textarea name="love" id="" cols="30" rows="10" v-model="postMsg.loveContent" class="com-text"></textarea>
            </div>
          </section>
          <button class="com-button" @click="postMyMsgHandel()">{{msgAll.post}}</button>
        </div>
      </section>
    </section>
    <section class="footer">
      <footer class="footer">© {{msgAll.footer}} 版权所有</footer>
    </section>
    <section class="loading" v-show="isLoading == 1">

    </section>
    <section class="error" v-show="isErrShow == 1">
      {{isErr}}
    </section>
    <section class="black" v-show="isGuess==1 || isComment==1" @click="clearBlack()">

    </section>
