      <h5 class="guess-small">已猜中{{guessOk}}次，被猜{{guessAll}}次</h5>
      <input class="guess-input" type="text" placeholder="猜一猜发起者的名字" v-model="guessName">
      <button class="guess-button" @click="guessStart()">开始猜测</button>
    </section>
    <section class="comment" v-show="isComment==1">
      <h3 class="comment-title">评论</h3>
      <div>
        <p v-for="(x,index) in listComment" :key="index" class="comment-p">
          <span>第{{index+1}}楼:</span>
          <span>{{x.content}}</span>
          <!-- <span>{{x.date}}</span> -->
        </p>
      </div>
      <input v-show="listComment.length<10"  class="comment-input" type="text" placeholder="我也要评论" v-model="postComment">
      <input v-show="listComment.length>=10" class="comment-input" type="text" value="评论已关闭" disabled="disabled">
      <button v-show="listComment.length<10" class="comment-button" @click="postComHandel()">开始评论</button>
      <button v-show="listComment.length>=10" class="comment-button" @click="isComment=0">评论已关闭</button>
    </section>
    <section class="control" v-show="controlShow==1">
      <p>我生君未生，君生我已老。</p>
      <p>我恨君生迟，君叹我生早。</p>
      <p>若得生同时，誓拟与君好。</p>
      <p>年岁不可更，怅惘知多少。</p>
      <p>咫尺似天涯，寸心难相表。</p>
      <p>我生君未生，君生我已老。</p>
      <p>来世愿同生，永作比翼鸟。</p>
      <p>和鸣相伴飞，天涯复海角。</p>
      <p>有日老难飞，互抱栖树杪。</p>
      <p>老死化树藤，情根亦缠绕。</p>
    </section>
    <section v-show="isLoading == 1">
      <div class="page">
          <div class="circle-loader">
              <div class="circle-line">
                  <div class="circle circle-blue"></div>
                  <div class="circle circle-blue"></div>
                  <div class="circle circle-blue"></div>
              </div>
              <div class="circle-line">
                  <div class="circle circle-yellow"></div>
                  <div class="circle circle-yellow"></div>
                  <div class="circle circle-yellow"></div>
              </div>
              <div class="circle-line">
                  <div class="circle circle-red"></div>
                  <div class="circle circle-red"></div>
                  <div class="circle circle-red"></div>
              </div>
              <div class="circle-line">
                  <div class="circle circle-green"></div>
                  <div class="circle circle-green"></div>
                  <div class="circle circle-green"></div>
              </div>
          </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msgAll:{},
      postMsg: {
        nickA: "",
        nameA: "",
        mateA: "0",
        aisAon: "1",
        nameB: "",
        mateB: "1",
        loveContent: "",
        date: new Date().toLocaleDateString(),
        praise: 0,
        guessOk: 0,
        guessAll: 0,
        comment: []
      },
      postMsgBak: {
        nickA: "",
        nameA: "",
        mateA: "0",
        aisAon: "1",
        nameB: "",
        mateB: "1",
        loveContent: "",
        date: new Date().toLocaleDateString(),
        praise: 0,
        guessOk: 0,
        guessAll: 0,
        comment: []
      },
      arrayOk: ["是", "否"],
      array: ["男", "女"],
      controlShow: 0,
      guessOk: 0,
      guessAll: 0,
      guessName: "",
      listComment: "",
      isBannerShow: 0,
      isLoading: 0,
      isGuess: 0,
      isComment: 0,
      headBg: [],
      headUl: [],
      isCheckLi: 0,
      loveAll: [],
      loveNM: [],
      loveSM: [],
      isErr: "",
      isErrShow: 0,
      isUpEnd: false,
      idComment: "",
      postComment: "",
      guessId:''
    };
  },

  components: {},

  methods: {
    msgShow(data) {
      return this.$http.get("https://love.app.api00.cn/api/" + data);
    },
    goRefresh() {
      this.$http
        .all([
          this.msgShow("all"),
          this.msgShow("anonymous"),
          this.msgShow("real"),
          this.msgShow("msgAll")
        ])
        .then(
          this.$http.spread((a, b, c,d) => {
            this.loveAll = a.data;
            this.loveNM = b.data;
            this.loveSM = c.data;
            this.msgAll = d.data
            this.isLoading = 0;
          })
        );
    },
    showMsgErr: function(data) {
      this.isErr = data;
      this.isErrShow = 1;
      setTimeout(() => {
        this.isErrShow = 0;
      }, 1000);
    },
    goTop: function(e) {
      // 一键回到顶部
      if (wx.pageScrollTo) {
        wx.pageScrollTo({
          scrollTop: 0
        });
      } else {
        wx.showModal({
          title: "提示",
          content:
            "当前微信版本过低，无法使用该功能，请升级到最新微信版本后重试。"
        });
      }
    },
    clickLiHandel(data) {
      this.isCheckLi = data;
    },
    showLoveAll() {},
    isUp(id) {
      // console.log(id);
      if (this.isUpEnd == false) {
        this.isLoading = 1;
        this.$http
          .post("https://love.app.api00.cn/api/isUp", { id: id })
          .then(data => {
            if (data.data.err == 0) {
              this.goRefresh();
              this.isUpEnd = true;
              this.showMsgErr("谢谢点赞");
            }
          });
      } else {
        this.showMsgErr("您已点过赞了");
      }
    },
    guessNameHandel(id, ok, all) {
      this.guessOk = ok;
      this.guessAll = all;
      this.guessId = id
      this.isGuess = 1;
    },
    guessStart() {
      // console.log(this.guessName);
      this.$http.post('https://love.app.api00.cn/api/guess',{id:this.guessId,nameA:this.guessName}).then(data=>{
        if(data.data.err == 0){
          this.isGuess = 0;
          this.showMsgErr('相似度为'+data.data.msg+'%')
          this.guessName = ''
        }else{
          this.isGuess = 0;
          this.showMsgErr('数据库炸了')
        }
        this.goRefresh()
      })
    },
    commentHandel(id, comment) {
      this.listComment = comment;
      this.isComment = 1;
      // console.log(id)
      this.idComment = id;
      // console.log(this.listComment);
    },
    postComHandel() {
      // console.log(this.postComment)
      this.$http
        .post("https://love.app.api00.cn/api/comment", {
          id: this.idComment,
          comment: this.postComment,
          date: new Date().toLocaleDateString()
        })
        .then(data => {
          if (data.data.err == 0) {
            this.showMsgErr(data.data.msg);
            this.goRefresh();
          } else {
            this.showMsgErr("出错了哦");
          }
          this.postComment = "";
          this.isComment = 0;
        });
    },
    clearBlack() {
      this.isGuess = 0;
      this.isComment = 0;
    },
    bindPickerOneChange(e) {
      this.postMsg.mateA = e.mp.detail.value;
    },
    bindPickerTwoChange(e) {
      this.postMsg.mateB = e.mp.detail.value;
    },
    bindPickerThrChange(e) {
      this.postMsg.aisAon = e.mp.detail.value;
    },
    postMyMsgHandel() {
      this.isLoading = 1;
      this.$http
        .post("https://love.app.api00.cn/api/postLove", this.postMsg)
        .then(data => {
          if (data.data.err == 1) {
            this.isLoading = 0;
            this.showMsgErr(data.data.msg);
          } else {
            this.$http
              .all([
                this.msgShow("all"),
                this.msgShow("anonymous"),
                this.msgShow("real"),
                this.msgShow("msgAll")
              ])
              .then(
                this.$http.spread((a, b, c,d) => {
                  this.loveAll = a.data;
                  this.loveNM = b.data;
                  this.loveSM = c.data;
                  this.msgAll = d.data
                  this.isLoading = 0;
                  this.postMsg = this.postMsgBak;
                  this.isCheckLi = 0;
                  this.goTop();
                })
              );
          }
        });
    }
  },

  created() {
    this.isLoading = 1;
    this.$http
      .all([
        this.msgShow("listBanner"),
        this.msgShow("ulText"),
        this.msgShow("all"),
        this.msgShow("control"),
        this.msgShow("anonymous"),
        this.msgShow("real"),
        this.msgShow("msgAll")
      ])
      .then(
        this.$http.spread((a, b, c, d, e, f,g) => {
          this.headBg = a.data;
          this.headUl = b.data;
          this.loveAll = c.data;
          this.controlShow = d.data;
          this.loveNM = e.data;
          this.loveSM = f.data;
          this.msgAll = g.data
          this.isLoading = 0;
          setInterval(() => {
            if (this.isBannerShow < this.headBg.length - 1) {
              this.isBannerShow++;
            } else {
              this.isBannerShow = 0;
            }
          }, 3000);
        })
      );
  }
};
</script>

