  font-weight: bolder;
  margin-top: 5vh;
  text-align: center;
}

.guess-small {
  font-size: 12px;
  color: gray;
  text-align: center;
  margin-top: 3vh;
}

.guess-input {
  width: 50vw;
  margin: 5vh auto;
  border: 1px solid lightgray;
  font-size: 18px;
}

.guess-button {
  width: 50vw;
  margin: 5vh auto;
  background-color: #ff5955;
  color: white;
  border: 0;
  border-radius: 10px;
  font-size: 15px;
}

.control {
  width: 100vw;
  height: 100vh;
  background-color: #01aaed;
  position: fixed;
  top: 0;
  left: 0;
}

.control > p {
  font-size: 18px;
  color: white;
  text-align: center;
  margin-top: 30px;
}

.comment {
  width: 70vw;
  padding-left: 2vw;
  padding-right: 2vw;
  position: fixed;
  top: 15vh;
  left: 13vw;
  background-color: white;
  border-radius: 20px;
  overflow: auto;
  overflow: hidden;
}

.comment-title {
  font-weight: bolder;
  margin-top: 3vh;
  text-align: center;
  margin-bottom: 2vh;
}

.comment-p > span:nth-of-type(1) {
  display: inline-block;
  font-size: 15px;
  margin-right: 20px;
  margin-left: 20px;
  height: 5vw;
  line-height: 5vw;
  overflow: hidden;
}

.comment-p > span:nth-of-type(2) {
  display: inline-block;
  width: 40vw;
  font-size: 15px;
  color: gray;
  height: 5vw;
  line-height: 5vw;
  overflow: hidden;
}

.comment-small {
  font-size: 12px;
  color: gray;
  text-align: center;
  margin-top: 3vh;
}

.comment-input {
  width: 50vw;
  margin: 5vh auto;
  border: 1px solid lightgray;
  font-size: 18px;
}

.comment-button {
  width: 50vw;
  margin: 5vh auto;
  background-color: #ff5955;
  color: white;
  border: 0;
  border-radius: 10px;
  font-size: 15px;
}
.com-cell {
  width: 70vw;
  padding: 3vw;
  height: 70vw;
  margin: 8vw auto;
  border-radius: 20px;
  position: relative;
  font-size: 15px;
}

.com-cell-boy {
  border: 1px dotted #01aaed;
  color: #01aaed;
}

.com-cell-girl {
  border: 1px dotted #ff5955;
  color: #ff5955;
  height: 30vw;
}

.com-cell-black {
  border: 1px dotted black;
  color: black;
  height: 20vw;
}

.com-cell-label {
  width: 25vw;
  height: 8vw;
  color: white;
  position: absolute;
  text-align: center;
  border-radius: 10px;
  position: absolute;
  top: -4vw;
  left: 10vw;
  line-height: 8vw;
}

.com-cell-label-boy {
  background-color: #01aaed;
}

.com-cell-label-girl {
  background-color: #ff5955;
}

.com-cell-label-black {
  background-color: black;
}

.nick-a,
.name-b {
  margin-top: 5vw;
}

.nick-input-b {
  border-bottom: 1px solid #01aaed;
  margin-bottom: 3vw;
}

.nick-input-g {
  border-bottom: 1px solid #ff5955;
  margin-bottom: 3vw;
}

.nick-input-n {
  border-bottom: 1px solid black;
  margin-bottom: 3vw;
}

.nick-sm {
  margin-top: 5vw;
  font-size: 12px;
  margin-bottom: 5vw;
}

.com-button {
  width: 80vw;
  margin: 2vh auto;
  background-color: #ff5955;
  color: white;
  border: 0;
  border-radius: 10px;
  font-size: 15px;
}

.footer {
  text-align: center;
  font-size: 12px;
  margin-top: 5vw;
  margin-bottom: 3vw;
  color: #aaa;
}

.com-div {
  width: 60vw;
  margin-left: 5vw;
  margin-right: 5vw;
  margin-top: 2vw;
  height: 18vw;
  overflow: hidden;
}

.com-text {
  width: 60vw;
  height: 18vw;
}

.error {
  width: 40vw;
  height: 40vw;
  background-color: black;
  position: fixed;
  top: 30vh;
  left: 30vw;
  border-radius: 20px;
  opacity: 0.66;
  line-height: 40vw;
  text-align: center;
  color: white;
  font-size: 15px;
}
</style>



